
#about {
  background: #eee;
}
.uk-card-title{
  width: 100%;
  font-weight: 400;
  letter-spacing: 1px;
  font-style: oblique;
}
p{
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.7;
}
.uk-button{
  font-weight: bold;
  letter-spacing: 2px;
  border: none;
  height: 45px;
  border-radius: 3px;
}
.uk-button:hover{background: rgba(0,0,0,0.1);
}
.uk-button:active{background: rgba(0,0,0,0.3);
}
.uk-card-title,
p,
.uk-button{
  font-family: 'Ubuntu', sans-serif !important;
  text-transform: capitalize;
  display: block;
}
.about-margin-top{
  padding-top: 150px;
}
.about-margin-bottom{
  margin-bottom: 80px;
}
.about-card{
  position: relative;
  display: inline-block;
  border-radius: 6px;
}
.about-card-header{
  position: relative;
  border-radius: 6px;
  margin-left: -80px;
  -webkit-box-shadow: 0 14px 25px rgba(0,0,0,0.16) !important;
          box-shadow: 0 14px 25px rgba(0,0,0,0.16) !important;
  margin-top: -50px;
}



